




















































































































import Vue from "vue";

import UnitPlanOa from "@/models/UnitPlanOa";
import UnitPlanWeek from '@/models/UnitPlanWeek';
import Oa from '@/models/Oa';

export default Vue.extend({
    name: "RomaAdvance",
    created () {
      this.selectedMenu = this.menu;
    },
    props: {
        unit: {
            type: Object,
            required: true,
        },
        height: {
            type: String,
            default: '514'
        },
        applyRowColor: {
            type: Boolean,
            default: true
        },
    },
    data() {
        return {
            expanded: [],
            request: null, // modificación del ie
            headers: [
                { text: '1', value: '1' },
                { text: '2', value: '2' },
                { text: '3', value: '3' },
                { text: '4', value: '4' },
                { text: '5', value: '5' },
            ],
        };
    },
    computed: {
        oasAdded () {
            let weeks = UnitPlanWeek.query()
                .where('unitplan_id', this.unit.id)
                .where('disabled', false)
                .with('ies', query => {
                    query.with('ie', q => {
                        q.with('oa')
                    })
                })
                .get();

            weeks.forEach(week => {
                console.log(week.ies.map(details => details.ie.oa_id));
                week.oas = Oa.query()
                    .whereIdIn(week.ies.map(details => details.ie.oa_id))
                    .with('ies', query => {
                      query.findIn(week.ies.map(i => i.id))
                    })
                    .get();
            });

            // console.log(weeks)

            return weeks;
        },
    },
    methods: {
        saveAdvance () {
            const msj = {
                show: true,
                text: '¡Avance de la unidad Guardada!',
                color: 'success',
            };

            this.$store.state.snackbar = msj;
        },
        advanceOa (ies: array) {
          const dones = ies.filter(ie => ie.done);
          let por = (100 * dones.length) / ies.length;

          return por.toFixed(0);
        },
        rowStyle (itemIndex: number, disabled: boolean) {
            const colors = ['--v-roma_rcolor-base', '--v-primary-base', '--v-secondary-base', '--v-roma_acolor-base'];
            let color = colors[itemIndex];
            color = `var(${colors[itemIndex % colors.length]})`;

            if (disabled || !this.applyRowColor) color = 'grey';

            return `border-left: 8px solid ${color};`;
        },
    },
});
